@import url(https://fonts.googleapis.com/css?family=Roboto:wght@300;400;500;600;700;800;900);
@import url(https://fonts.googleapis.com/css2?family=Amatic+SC:wght@400;700&display=swap);
@import-normalize;

*, *:before, *:after {
  margin:0;
  padding:0;
  box-sizing:border-box;
}

body {
  font-family: 'Roboto', sans-serif;
  color:white;
}

body, .horizontal-parent {overflow:hidden;}

.horizontal-parent {
  position:absolute;
  top:0;
  left:0;
  width:100vw;
  height:100vh;
}

$slides:5;

.horizontal-child {
  position:relative;
  display:block;
  width:100vh;
  height:100vw;
  overflow:scroll;
  transform-origin:100% 0%;
  transform:rotate(-90deg) translateY(-100vh);
  scroll-behavior: smooth;
  scroll-snap-type: x-mandatory;
  
  // each slide should be the size of the viewport
  .slide {
    z-index:2;
    display:flex;
    position:absolute;
    width:100vw;
    height:100vh;
    overflow:hidden;
    text-align:center;
    transform-origin:0% 0%;

    
    &:after {
      content:'';
      position:absolute;
      // background-attachment fixed does the magic on the background
      background-attachment:fixed;
      background-position:0 0;
      background-repeat:no-repeat;
      z-index:-1;
    }

    // As every .slide is positioned absolute, we translate each on the x-axis
    // by nth * viewport-width >> so they sit next to each other
    // like this: □□□□
    // + rotate them by 90deg
    // + slide them up by translate them on the y-axis by 100 viewport-height
    @for $slide from 2 through $slides {
      $perc:1 / $slides;
      $i: ($slide - 1) * $perc;
      $color:adjust-hue( red, ($slide * 10%) );
      &:nth-child(#{$slide}) {
        transform:rotate(90deg) translateY(-100%) translateX(($slide - 1) * 99.9vw);
        background-color:$color;
        
        // trail and error
        // there's a logic behind it, but I got screwed up by all the rotations
        &:after {
          top: $slide * -100%;
          left: $slide * -100%;
          width: $slide * 200%;
          height: $slide * 200%;
          background-size:($slide * 200%) ($slide * 200%);
          transform:scale(1-$i);
        }
      }    
    }

    &:nth-child(2) {
      background-color: #ff5500;
    }

    &:nth-child(3) {
      background-color: #A2BA0C;
    }
  
  // center the content of each slide
  // remember: we set the parent element to display:flex
  .slide-heading {
    margin:auto;
  }

  img {
      display:block;
      width:50%;
      height:auto;
      margin:auto;
    }
  }
}

#hand-left {fill:red;}

.slide-heading h1 {
  font-size:7vw;
  font-weight:300;
  text-transform:uppercase;
  letter-spacing:1vw;
  transform:rotate(180deg);
}

.slide-heading h2 {
  font-size:2vw;
  font-weight:300;
  text-transform:uppercase;
  letter-spacing:1vw;
}

.horizontal-child {
  &::-webkit-scrollbar {
    display:none;
  }
  
  &::-webkit-scrollbar-track {
    display:none;
  }
  
  &::-webkit-scrollbar-thumb {
    display:none;  
  }
  
  &::-webkit-scrollbar-thumb:window-inactive {
	  display:none; 
  }
}

 
@mixin transition($transition...) {
  -moz-transition:    $transition;
  -o-transition:      $transition;
  -webkit-transition: $transition;
  transition:         $transition;
}

@mixin box-shadow($box-shadow...) {
  -webkit-box-shadow: $box-shadow;
  -moz-box-shadow: $box-shadow;
  box-shadow: $box-shadow;
}

@mixin vertical-align-middle {
  position: absolute;
  top: 50%; left: 50%;
  transform: translate(-50%,-50%);
}

.fa-spin-scss/*.fa-spin-hover:hover*/ {
   -webkit-animation: fa-spin 2s infinite linear;
   -moz-animation: fa-spin 2s infinite linear;
   -o-animation: fa-spin 2s infinite linear;
   animation: fa-spin 2s infinite linear;
}

.no-select {
  -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Chrome/Safari/Opera */
     -khtml-user-select: none; /* Konqueror */
       -moz-user-select: none; /* Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently not supported by any browser */
  cursor: default; // Don't bring up select cursor. Doesn't override other cursors.
}



.game-area {
  padding: 50px;
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.game-grid {
  @extend .no-select;
  position: relative;
  width: 400px !important;
  height: 400px;
  margin: 0 auto;
  font-size: 0;
  background-color: #A2BA0C;
  border-radius: 20px;
  // Lazy screen arrangement:
  float: right;

  @media (min-width: 1200px) {
    width: 600px !important;
    height: 600px;
  }
}

$grid-padding: 2%;
$cell-margin: 1%;

.cell {
  position: absolute;
  display: inline-block;
  margin: $cell-margin;
  border-radius: 50%;//10px;
  background-color: #f2fcbd;
  color: rgb(0, 71, 0);
  text-align: center;
  cursor: pointer;
  @include transition(background-color 0.25s);
}

.guess {
  color: #A2BA0C;
  cursor: not-allowed;
}

.trace, .cell:hover {
  @include transition(background-color 0.25s);
  background-color: #fff;
}

$total: 100%;
$c5: (($total - (2*$grid-padding)) / 5) - (2*$cell-margin);
$c6: (($total - (2*$grid-padding)) / 6) - (2*$cell-margin);
$c7: (($total - (2*$grid-padding)) / 7) - (2*$cell-margin);
$c8: (($total - (2*$grid-padding)) / 8) - (2*$cell-margin);
$c9: (($total - (2*$grid-padding)) / 9) - (2*$cell-margin);
$c10: (($total - (2*$grid-padding)) / 10) - (2*$cell-margin);

.cell5  { width: $c5; height: $c5; font-size: 40px; }
.cell6  { width: $c6; height: $c6; font-size: 36px; }
.cell7  { width: $c7; height: $c7; font-size: 32px; }
.cell8  { width: $c8; height: $c8; font-size: 28px; }
.cell9  { width: $c9; height: $c9; font-size: 24px; }
.cell10  { width: $c10; height: $c10; font-size: 16px; 
  @media (min-width: 1200px) {
    font-size: 24px;
  }
}

.vam {
  @include vertical-align-middle();
}

.clue-area {
  margin: 10px auto;
  //width: 400px;
  //height: 70px;
  overflow: hidden;
  text-align: left;
  float: left;
  color: rgb(1, 66, 1);

  div {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}

h3 {
  font-size: 36px;
}

.clues {
}

.clue {
  font-size: 16px;
  margin: 10px;
  @include transition(color 1s);

  @media (min-width: 1200px) {
    font-size: 24px;
  }
}
.clue-answered {
  @include transition(color 1s);
  color: #7e006d;
}
i {
  font-style: italic;
}
.unvisible {
  display: none;
}


#shadowBox {
  overflow: hidden;
}

.rainbow {
  text-align: center;
  font-size: 7vw;
  font-weight: 300;
  text-transform: uppercase;
  letter-spacing: 1vw;
}
.rainbow_text_animated {
  background: linear-gradient(to right, #6666ff, #0099ff , #00ff00, #ff3399, #6666ff);
  background-clip: text;
  color: transparent;
  animation: rainbow_animation 6s ease-in-out infinite;
  background-size: 400% 100%;
  position: relative;

  &:before {
    content: '';
    height: 100%;
    width: 3px;
    position: absolute;
    left: calc(100% - 1px);
    background: #ffaa00;
    z-index: 9999;
  }
}

@keyframes rainbow_animation {
  0%,100% {
      background-position: 0 0;
  }

  50% {
      background-position: 100% 0;
  }
}


@-webkit-keyframes scroll {
  0% {
      -webkit-transform: translate(0, 0);
      transform: translate(0, 0);
  }
  100% {
      -webkit-transform: translate(-100%, 0);
      transform: translate(-100%, 0)
  }
}

@-moz-keyframes scroll {
  0% {
      -moz-transform: translate(0, 0);
      transform: translate(0, 0);
  }
  100% {
      -moz-transform: translate(-100%, 0);
      transform: translate(-100%, 0)
  }
}

@keyframes scroll {
  0% {
      transform: translate(0, 0);
  }
  100% {
      transform: translate(-100%, 0)
  }
}

.marquee {
  display: block;
  width: 100%;
  white-space: nowrap;
  overflow: hidden;
  position: absolute;
  bottom: 0;

  img {
    max-height: 16px;
  }
}

.marquee span {
  font-size: 16px;
  display: inline-block;
  padding-left: 100%;
  font-family: 'Amatic SC';
  font-weight: 400;
  color: black;
  -webkit-animation: scroll 20s infinite linear;
  -moz-animation: scroll 20s infinite linear;
  animation: scroll 20s infinite linear;
}

.bg-video {
  position: absolute;
  object-fit: cover;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.video {
  margin: 30px auto 60px;
  position: relative;
  z-index: 9;
  border: 6px solid #6bab21;
  padding: 6px 0;
}

.star-wars {
  overflow: hidden;
  font-family: 'Roboto';
  padding: 50% 0 0 0;
  position: absolute !important;
  z-index: 99 !important;
  right: -100%;
  left: 0 !important;
  top: -100% !important;
  background-image: linear-gradient(rgb(20, 20, 20), rgb(20, 20, 20))  !important;

  transform: rotate(90deg) translateY(-100%) translateX(99.9vw);

  &:after {
    background-image: linear-gradient(rgb(20, 20, 20), rgb(20, 20, 20))  !important;

  }

  &:before {
    content: '';
    position: fixed;
    z-index: 99;
    top: 0;
    width: 100%;
    height: 50%;
    background: linear-gradient((180deg, rgba(20, 20, 20, 1) 40%, rgba(20, 20, 20, 0) 100%));
  }

  .wrapper {
    display: flex;
    margin: 0 auto;
    height: 100%;
    width: 60%;
    perspective: 450px;
  }

  .scroll-text {
    color: black;
    font-size: 3vw;
    line-height: 1.5em;
    font-weight: bold !important;
    text-align: justify;
    position: relative;
    animation: scroll-wars 60s linear forwards;
    color: #ffc909;
  }
  
  h1, h2 {
    text-align: center;
    margin: 0.67em 0;
    line-height: 1;
  }
}

@keyframes scroll-wars {
  from { top: 0; transform: translateZ(0) rotateX(20deg)}
  to { top: -7500px; transform: translateZ(-2500px) rotateX(21deg)}
}

.anim-stars {
  display: flex;
